.main {
  margin: 20px 0;
  height: 100%;

  &-logo {
    margin-bottom: 15px;
    text-align: center;

    .logo {
      margin: 0 auto;
      width: 50px;
      height: 50px;

      img {
        width: 100%;
      }
    }
  }

  .items {
    display: grid;
    justify-content: center;

    .link {
      text-decoration: none;
    }
  }

}